import React from 'react';
import styled from 'styled-components';
import aboutBackground from '../image/aboutBackground.jpg';
import LinkBtn from './LinkBtn';

const AboutWrap = styled.div`
  background: url(${aboutBackground}) center center/ cover no-repeat;
  width: calc(100% - 24rem);
  margin-left: 24rem;
  min-height: 100vh;
  position: relative;

  @media (${(props) => props.theme.size.mobile}) {
    width: 100%;
    margin-left: 0;
  }

  .blur {
    min-height: 100vh;
    background: ${(props) => props.theme.about.blur};
  }

  .desc_wrap {
    position: absolute;
    top: 24rem;
    padding: 0 8rem;
    z-index: 1;

    @media (max-width: 1400px) {
      padding: 0 4rem;
    }
    @media (${(props) => props.theme.size.mobile}) {
      top: 18rem;
      padding: 0 1.6rem;
    }
  }
  .desc {
    color: ${(props) => props.theme.about.descColor};
    font-weight: 500;
    font-size: 2rem;
    line-height: 1.8;
    max-width: 56rem;
    word-break: keep-all;

    @media (${(props) => props.theme.size.mobile}) {
      font-size: 1.6rem;
    }
  }
  .desc ~ .desc {
    margin-top: 1rem;
  }
`;

const LinkWrap = styled.div`
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
  top: 52rem;
  right: 30%;

  @media (max-width: 1400px) {
    right: 8%;
  }
  @media (${(props) => props.theme.size.mobile}) {
    display: none;
  }
`;
export default function TabAbout() {
  return (
    <>
      <AboutWrap>
        <div className='blur'></div>
        <div className='desc_wrap'>
          <p className='desc'>
            !
            <br />
                   
               .
          </p>
          <p className='desc'>
                   
                    
            .
          </p>
          <p className='desc'>
                     
            .       
            !
          </p>
        </div>
        <LinkWrap>
          {/* <LinkBtn
            title=' '
            linkUrl='https://distinct-feels-7f8.notion.site/ccc7154587a543b298324a8ad7d3ebe4'
          /> */}
          <LinkBtn
            title=' '
            linkUrl='https://distinct-feels-7f8.notion.site/1b9f7bfe6bfd4e27846b77f275be8ba6?pvs=4'
          />
        </LinkWrap>
      </AboutWrap>
    </>
  );
}
