import React from 'react';
import styled from 'styled-components';
import LinkBtn from './LinkBtn';

const SkillWrap = styled.div`
  width: calc(100% - 24rem);
  margin-left: 24rem;
  min-height: 100vh;
  position: relative;
  background-image: ${(props) => props.theme.about.skillBg};
  color: ${(props) => props.theme.mainColor};
  padding: 6rem;
  display: flex;

  @media (${(props) => props.theme.size.mobile}) {
    width: 100%;
    margin-left: 0;
    padding: 0 2rem;
  }
`;
const Contents = styled.div`
  position: absolute;
  width: calc(100% - 12rem);
  padding-top: 9rem;
  z-index: 1;
  overflow-y: auto;
  display: flex;
  justify-content: space-around;
  max-height: calc(100vh - 16rem);

  &::-webkit-scrollbar {
    display: none;
  }

  @media (${(props) => props.theme.size.mobile}) {
    padding-top: 0;
    width: calc(100% - 2rem);
    max-height: calc(100vh - 24rem);
    top: 12rem;
    justify-content: normal;
    flex-wrap: wrap;

    .link_wrap {
      display: none;
    }
  }

  .link_wrap {
    position: absolute;
    top: 6rem;
    right: 0.8rem;
  }

  .left {
    margin-right: 4rem;
  }
`;
const Box = styled.div`
  margin-top: 2.8rem;

  .text_wrap {
    margin-top: 1.6rem;
    max-width: 52rem;

    p {
      font-size: 1.6rem;
      margin-top: 0.4rem;
      line-height: 1.5;
    }
  }
`;
const Tag = styled.p`
  display: inline-block;
  padding: 0 2rem;
  background: #000;
  border-radius: 1rem;
  font-size: 2rem;
  height: 4rem;
  line-height: 4rem;
  color: #fff;
`;
